<template>
  <div id="card">
    <div v-if="book">
      <b-card
        :title="book.name"
        :img-src="getImage"
        :img-alt="book.photo.filename"
        img-top
        img-height="180px"
        img-width="100px"
        tag="article"
        style="max-width: 100%;"
        class="mb-4"
      >
        <div class="text">
          <b-card-text>Genre: {{ book.genre }}</b-card-text>
          <b-card-text>Author: {{ authorName ? authorName : book.author.name ? book.author.name : 'Unknown'}}</b-card-text>
          <b-card-text>Release Date: {{ date }}</b-card-text>
          <b-card-text>Added By : {{ `${book.user_add ? book.user_add : 'Anonymous' }` }}</b-card-text>
          <b-card-text>Description :</b-card-text>
          <b-card-text class="desc ml-2">{{ `${desc}...` }}</b-card-text>
        </div>
        <b-button
          href="#"
          @click="bookDetail(book._id)"
          class="btn-details"
          variant="primary"
        >Details</b-button>
      </b-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "CardBook",
  props: {
    book: Object,
    authorName: String,
  },
  data() {
    return {
      img: require("@/assets/img/card-1.jpg"),
      descMore: "",
      month: [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "July",
        "Aug",
        "Sept",
        "Oct",
        "Nov",
        "Des",
      ],
      desc: "",
      show: {
        btn: false,
        desc: false,
      },
    };
  },
  mounted() {
    const desc = this.descSlice();
    this.desc = desc;
  },
  methods: {
    toggleDesc() {
      this.show.desc = !this.show.desc;
      this.show.btn = !this.show.btn;
    },
    descSlice() {
      // Return when book doesn't have description
      if (!this.book.description) return "This Book doesn't have description";
      // Split book description
      const desc = this.book.description.split(" ");
      if (desc.length > 10) return desc.slice(0, 10).join(" ");
      return this.book.description;
    },
    bookDetail(id) {
      this.$router.push({ name: "book-detail", params: { bookId: id } });
    },
  },
  computed: {
    date() {
      const d = new Date(this.book.date);
      const date = d.getDate();
      const indexMonth = d.getMonth();
      const fullYear = d.getFullYear();
      return `${date}-${this.month[indexMonth]}-${fullYear}`;
    },
    getImage() {
      const filename = this.book.photo.filename;
      if (!filename) return this.img;
      return `http://localhost:5000/img-card/${filename}`;
    },
  },
};
</script>

